* {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-size: 18px;
}

h1 {
  font-size: 2rem;
}

h2{
  font-size: 1.5rem;
}

a {
  font-weight: bold;
  text-decoration: none;
}

ul {
  display:flex;
  flex-direction: row;
  justify-content: space-between;
  font-size: larger;
  color: white;
}

.footer {
  text-align: center;
}

.navitem-indent {
  padding-left: 300px;
}

.navbar-nav {
  margin-left: 30px;
  margin-right: 30px;
}

.navbar-container {
  background: black;
}

#personal-img {
  align-content: center;
}

.container {
  width: 85%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.wrapper {
  padding-top: 50px;
  justify-content: space-around;
  height: 100%;
  display: flex;
  overflow: auto;
  flex-flow: row wrap;
  padding: 10px;
  align-content: flex-start;
}

.card {
  border-radius: 5px;
  height: 400px;
  margin: 2rem;
  position: relative;
  width: 400px;
}

.card .img-container {
  height: 75%;
  overflow: hidden;
  text-align: center;
  border-radius: 10px;
  background-color: var(--white);
}

.card .img-container img {
  width: 100%;
  padding: 25px;
  margin-bottom: 20px;
  background-color: var(--white);
}

.card .content {
  font-size: 18px;
  background-color: var(--white);
  color: var(--cadet-blue);
  font-weight: bold;
  text-align: center;
}

.card-title{
  margin-top: 10px;
  color: var(--eerie-black);
}

li {
  list-style: none;
}
